@import 'core/reset';
@import 'core/responsive';
@import 'core/mixins';
@import 'core/function';
@import 'core/variables';
@import 'pages/landing';
@import 'layout/main-create';
@import 'layout/Share';
@import 'layout/create-card';
@import 'layout/footer';
@import 'components/jsarrow';
@import 'components/palette';


.preview {
    background: linear-gradient(to bottom, $color-preview1, $color-preview2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: rem(38);
    border-radius: rem(6);

    @include tablet {
        flex-direction: column;
        padding-bottom: rem(200);
        margin-right: rem(30);
        flex-grow: 1;
    }

    @include desktop {
        flex-grow: 2;
    }

    &__align {
        min-width: 60%;
        display: flex;
        flex-direction: column;
        gap: rem(30);
    }

    &__button {
        font-family: $font-Open;
        font-size: rem(12);
        max-width: rem(80);
        padding: rem(6);
        border: solid white rem(1);
        border-radius: rem(15);
        background: transparent;
        text-transform: uppercase;
        color: $color_gunMetal;
    }
}